    }
}

data class SocialMedia(
    val name: String,
    val icon: @Composable () -> Unit
)

val blinkAnimation = Keyframes {
    from { Modifier.opacity(0f) }
    to { Modifier.opacity(1f) }
}

@Composable
fun componentProject(
    icon: String,
    title: String,
    description: String,
    tags: List<String>,
    isBuy: Boolean = false
) {
    Column(
        modifier = Modifier
            .padding(10.px)
            .margin(top = 8.px)
            .fillMaxWidth(50.percent)
    ) {
        Image(
            src = icon,
            modifier = Modifier
                .height(175.px)
                .fillMaxWidth(100.percent)
                .borderRadius(10.px)
        )

        Column(
            modifier = Modifier
                .fillMaxWidth()
                .padding(5.px)
        ) {
            P(
                attrs = TextTitleProjectContentApp
                    .toModifier()
                    .align(Alignment.Start)
                    .toAttrs()
            ) {
                Text(
                    value = title
                )
            }

            P(
                attrs = TextDescriptionProjectContentApp
                    .toModifier()
                    .align(Alignment.Start)
                    .toAttrs()
            ) {
                Text(
                    value = description
                )
            }

            Row(
                verticalAlignment = Alignment.CenterVertically,
                horizontalArrangement = Arrangement.spacedBy(3.px)
            ) {
                tags.forEachIndexed { index, tag ->
                    P(
                        attrs = TextNormalStyleContentApp
                            .toModifier()
                            .color(Color("#6CACE4"))
                            .toAttrs()
                    ) {
                        val symbol = if (index != tags.size - 1) {
                            " -"
                        } else {
                            ""
                        }
                        Text(value = "$tag$symbol")
                    }
                }
            }

            Row(
                modifier = SocialMediaStyle
                    .toModifier()
                    .cursor(Cursor.Pointer)
                    .margin(top = 20.px)
                    .fillMaxWidth()
                    .styleModifier {
                        property("border", "1px solid #833ab4")
                        property("border-radius", "6px")
                        property("border", "double 1px transparent")
                        property(
                            "background-image",
                            "linear-gradient(black, black), linear-gradient(90deg, #833ab4, #DD5789, #fcb045)"
                        )
                        property("background-origin", "border-box")
                        property("background-clip", "content-box, border-box")
                    },
                verticalAlignment = Alignment.CenterVertically,
                horizontalArrangement = Arrangement.Center
            ) {
                if (isBuy) {
                    FaCartShopping(
                        modifier = Modifier.color(color = Color.white).margin(top = 8.px, bottom = 8.px)
                    )

                    P(
                        attrs = TextNormalStyleContentApp
                            .toModifier()
                            .margin(left = 5.px)
                            .fontFamily("Inter")
                            .toAttrs()
                    ) {
                        Text(
                            value = "Disponível para compra"
                        )
                    }
                } else {
                    FaGithub(
                        modifier = Modifier.color(color = Color.white).margin(top = 8.px, bottom = 8.px)
                    )

                    P(
                        attrs = TextNormalStyleContentApp
                            .toModifier()
                            .margin(left = 5.px)
                            .fontFamily("Inter")
                            .toAttrs()
                    ) {
                        Text(
                            value = "Ir para o projeto"
                        )
                    }
                }
            }
        }
    }
}

@Composable
fun SocialMediaButton(text: String, icon: @Composable () -> Unit) {
    Column(
        modifier = styleHover.toModifier(),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        P(attrs = TextStyleHeaderApp.toModifier().toAttrs()) {
            Text(value = text)
        }
        icon()
    }
}

data class ProjectData(
    val icon: String,
    val title: String,
    val description: String,
    val tags: List<String>,
    var isBuy: Boolean = false
)

@Composable
fun ContentApp(modifier: Modifier = Modifier) {
    val projectList = listOf(
        ProjectData(
            icon = "ic_project_casino_site.png",
            title = "Casino Layout",
            description = "Um layout de casino profissional",
            tags = listOf("Android Native", "Kotlin", "Jetpack Compose"),
            isBuy = true
        ),
        ProjectData(
            icon = "ic_project_ecommerce_site.png",
            title = "Ecommerce Layout",
            description = "Um layout de Ecommerce de Tênis",
            tags = listOf("Android Native", "Kotlin", "Jetpack Compose")
        ),
        ProjectData(
            icon = "ic_project_frdguard_site.png",
            title = "FridaGuard App",
            description = "Um app para assegurar o script Frida ser executado",
            tags = listOf("Frida", "TCP", "Kotlin", "Jetpack Compose", "Javascript")
        ),
        ProjectData(
            icon = "ic_project_codecheck_site.png",
            title = "Login Check",
            description = "Uma tela de login, com foco na parte do backend",
            tags = listOf("Javascript", "Html", "Css", "SendGrid", "Nodejs")
        ),
    )

    Column(
        modifier = modifier
            .padding(8.px)
            .fillMaxWidth(),
        horizontalAlignment = Alignment.Start,
        verticalArrangement = Arrangement.spacedBy(110.px)
    ) {
        Column(
            modifier = Modifier
                .width(Width.FitContent)
                .padding(10.px)
                .align(Alignment.CenterHorizontally)
        ) {
            P(
                attrs = TextStyleContentApp
                    .toModifier()
                    .toAttrs()
            ) {
                Text("Olá, eu sou")
            }

            P(
                attrs = TextHeaderName
                    .toModifier()
                    .backgroundImage(
                        linearGradient(LinearGradient.Direction.ToRight) {
                            add(Color("#9845E8"))
                            add(Color("#33D2FF"), 55.percent)
                            add(Color("#DD5789"), 100.percent)
                        }
                    )
                    .backgroundClip(BackgroundClip.Text)
                    .color(Color.transparent)
                    .toAttrs()
            ) {
                Text("João Victor R.")
            }

            P(
                attrs = TextPresentationContentApp
                    .toModifier()
                    .widthIn(min = 300.px, max = 500.px)
                    .letterSpacing(2.px)
                    .styleModifier {
                        property("word-wrap", "break-word")
                        property("white-space", "normal")
                    }
                    .toAttrs()
            ) {
                Text(
                    value = "Sou desenvolvedor especializado em Android nativo, com mais de dois anos de experiência em diversas linguagens. Durante essa trajetória, trabalhei desde a criação de APIs até o desenvolvimento de interfaces frontend de alta qualidade, sempre buscando unir um bom conhecimento de backend a um design intuitivo."
                )
            }

            Row(
                modifier = Modifier
                    .margin(top = 50.px),
                verticalAlignment = Alignment.CenterVertically,
                horizontalArrangement = Arrangement.spacedBy(5.px)
            ) {
                val listSocialMedia = listOf(
                    SocialMedia("Github") {
                        FaGithub(
                            modifier = Modifier
                                .color(color = Color.white)
                                .margin(left = 8.px)
                        )
                    },
                    SocialMedia("Lnkedin") {
                        FaLinkedin(
                            modifier = Modifier
                                .color(color = Color.white)
                                .margin(left = 8.px)
                        )
                    },
                    SocialMedia("Send Email") {
                        FaEnvelope(
                            modifier = Modifier
                                .color(color = Color.white)
                                .margin(left = 8.px)
                        )
                    },
                )

                listSocialMedia.forEach {
                    Row(
                        modifier = SocialMediaStyle
                            .toModifier()
                            .cursor(Cursor.Pointer)
                            .width(Width.FitContent)
                            .height(35.px)
                            .styleModifier {
                                property("border", "1px solid #833ab4")
                                property("border-radius", "6px")
                                property("border", "double 1px transparent")
                                property(
                                    "background-image",
                                    "linear-gradient(black, black), linear-gradient(90deg, #833ab4, #fd1d1d, #9845E8)"
                                )
                                property("background-origin", "border-box")
                                property("background-clip", "content-box, border-box")
                            },
                        verticalAlignment = Alignment.CenterVertically,
                        horizontalArrangement = Arrangement.Center
                    ) {
                        it.icon()

                        P(
                            attrs = TextStyleContentApp
                                .toModifier()
                                .color(Color.gray)
                                .fontSize(13.px)
                                .toAttrs()
                        ) {
                            Text(it.name)
