                    }
                }
            }
        }

        Column(
            modifier = Modifier
                .margin(top = 40.px)
                .align(Alignment.CenterHorizontally)
                .fillMaxWidth()
        ) {
            HorizontalDivider(
                modifier = Modifier
                    .border(0.01.px, LineStyle.Solid, Color.gray)
