            SimpleGrid(
                numColumns(1, sm = 2, md = 2),
                modifier = Modifier.fillMaxWidth()
            ) {
                projectList.forEach { project ->
                    Row(
                        modifier = Modifier.fillMaxWidth(),
                        horizontalArrangement = Arrangement.Center
                    ) {
                        componentProject(
                            icon = project.icon,
                            title = project.title,
                            description = project.description,
                            tags = project.tags,
                            isBuy = project.isBuy
                        )
                    }
                }
            }
        }
    }
}

